<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error("请输入正确的身份证号码"));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("请输入正确的URL地址"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error("请输入正确的手机号码"));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error("请输入正确的电话号码"));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error("请输入正确的邮箱地址"));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				xianhuamingcheng : false,
				xianhualeixing : false,
				xianhuatupian : false,
				xianhuaguige : false,
				xianhuachandi : false,
				xianhuahuayu : false,
				shiyongchangjing : false,
				songliduixiang : false,
				xianhuaxiangqing : false,
				onelimittimes : false,
				alllimittimes : false,
				thumbsupnum : false,
				crazilynum : false,
				clicktime : false,
				reversetime : false,
				clicknum : false,
				price : false,
				vipprice : false,
			},
			
			
			ruleForm: {
				xianhuamingcheng: '',
				xianhualeixing: '',
				xianhuatupian: '',
				xianhuaguige: '',
				xianhuachandi: '',
				xianhuahuayu: '',
				shiyongchangjing: '',
				songliduixiang: '',
				xianhuaxiangqing: '',
				onelimittimes: '-1',
				alllimittimes: '-1',
				clicktime: '',
				reversetime: '',
				price: '',
			},
		
			xianhualeixingOptions: [],
			
			rules: {
				xianhuamingcheng: [
					{ required: true, message: '鲜花名称不能为空', trigger: 'blur' },
				],
				xianhualeixing: [
					{ required: true, message: '鲜花类型不能为空', trigger: 'blur' },
				],
				xianhuatupian: [
				],
				xianhuaguige: [
				],
				xianhuachandi: [
				],
				xianhuahuayu: [
				],
				shiyongchangjing: [
				],
				songliduixiang: [
				],
				xianhuaxiangqing: [
				],
				onelimittimes: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				alllimittimes: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				thumbsupnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				crazilynum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				clicktime: [
				],
				reversetime: [
				],
				clicknum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				price: [
					{ required: true, message: '价格不能为空', trigger: 'blur' },
					{ validator: validateNumber, trigger: 'blur' },
				],
				vipprice: [
					{ validator: validateNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='xianhuamingcheng'){
							this.ruleForm.xianhuamingcheng = obj[o];
							this.ro.xianhuamingcheng = true;
							continue;
						}
						if(o=='xianhualeixing'){
							this.ruleForm.xianhualeixing = obj[o];
							this.ro.xianhualeixing = true;
							continue;
						}
						if(o=='xianhuatupian'){
							this.ruleForm.xianhuatupian = obj[o];
							this.ro.xianhuatupian = true;
							continue;
						}
						if(o=='xianhuaguige'){
							this.ruleForm.xianhuaguige = obj[o];
							this.ro.xianhuaguige = true;
							continue;
						}
						if(o=='xianhuachandi'){
							this.ruleForm.xianhuachandi = obj[o];
							this.ro.xianhuachandi = true;
							continue;
						}
						if(o=='xianhuahuayu'){
							this.ruleForm.xianhuahuayu = obj[o];
							this.ro.xianhuahuayu = true;
							continue;
						}
						if(o=='shiyongchangjing'){
							this.ruleForm.shiyongchangjing = obj[o];
							this.ro.shiyongchangjing = true;
							continue;
						}
						if(o=='songliduixiang'){
							this.ruleForm.songliduixiang = obj[o];
							this.ro.songliduixiang = true;
							continue;
						}
						if(o=='xianhuaxiangqing'){
							this.ruleForm.xianhuaxiangqing = obj[o];
							this.ro.xianhuaxiangqing = true;
							continue;
						}
						if(o=='onelimittimes'){
							this.ruleForm.onelimittimes = obj[o];
							this.ro.onelimittimes = true;
							continue;
						}
						if(o=='alllimittimes'){
							this.ruleForm.alllimittimes = obj[o];
							this.ro.alllimittimes = true;
							continue;
						}
						if(o=='thumbsupnum'){
							this.ruleForm.thumbsupnum = obj[o];
							this.ro.thumbsupnum = true;
							continue;
						}
						if(o=='crazilynum'){
							this.ruleForm.crazilynum = obj[o];
							this.ro.crazilynum = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='reversetime'){
							this.ruleForm.reversetime = obj[o];
							this.ro.reversetime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='price'){
							this.ruleForm.price = obj[o];
							this.ro.price = true;
							continue;
						}
						if(o=='vipprice'){
							this.ruleForm.vipprice = obj[o];
							this.ro.vipprice = true;
							continue;
						}
				}
				










				this.ruleForm.onelimittimes='-1'

				this.ruleForm.alllimittimes='-1'







			}
			
			
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					
					var json = data.data;
				} else {
					this.$message.error(data.msg);
				}
			});
			
            this.$http({
				url: `option/xianhualeixing/xianhualeixing`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.xianhualeixingOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `xianhuaxinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        this.ruleForm.xianhuaxiangqing = this.ruleForm.xianhuaxiangqing.replace(reg,'../../../djangog6zon/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {






	if(this.ruleForm.xianhuatupian!=null) {
		this.ruleForm.xianhuatupian = this.ruleForm.xianhuatupian.replace(new RegExp(this.$base.url,"g"),"");
	}































var objcross = this.$storage.getObj('crossObj');

      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
       this.$refs["ruleForm"].validate(valid => {
         if (valid) {
		 if(crossrefid && crossuserid) {
			 this.ruleForm.crossuserid = crossuserid;
			 this.ruleForm.crossrefid = crossrefid;
			let params = { 
				page: 1, 
				limit: 10, 
				crossuserid:this.ruleForm.crossuserid,
				crossrefid:this.ruleForm.crossrefid,
			} 
			this.$http({ 
				url: "xianhuaxinxi/page", 
				method: "get", 
				params: params 
			}).then(({ 
				data 
			}) => { 
				if (data && data.code === 0) { 
				       if(data.data.total>=crossoptnum) {
					     this.$message.error(this.$storage.get('tips'));
					       return false;
				       } else {
					 this.$http({
					   url: `xianhuaxinxi/${!this.ruleForm.id ? "save" : "update"}`,
					   method: "post",
					   data: this.ruleForm
					 }).then(({ data }) => {
					   if (data && data.code === 0) {
					     this.$message({
					       message: "操作成功",
					       type: "success",
					       duration: 1500,
					       onClose: () => {
						 this.parent.showFlag = true;
						 this.parent.addOrUpdateFlag = false;
						 this.parent.xianhuaxinxiCrossAddOrUpdateFlag = false;
						 this.parent.search();
						 this.parent.contentStyleChange();
					       }
					     });
					   } else {
					     this.$message.error(data.msg);
					   }
					 });

				       }
				} else { 
				} 
			});
		 } else {
			 this.$http({
			   url: `xianhuaxinxi/${!this.ruleForm.id ? "save" : "update"}`,
			   method: "post",
			   data: this.ruleForm
			 }).then(({ data }) => {
			   if (data && data.code === 0) {
			     this.$message({
			       message: "操作成功",
			       type: "success",
			       duration: 1500,
			       onClose: () => {
				 this.parent.showFlag = true;
				 this.parent.addOrUpdateFlag = false;
				 this.parent.xianhuaxinxiCrossAddOrUpdateFlag = false;
				 this.parent.search();
				 this.parent.contentStyleChange();
			       }
			     });
			   } else {
			     this.$message.error(data.msg);
			   }
			 });
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.xianhuaxinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    xianhuatupianUploadChange(fileUrls) {
	    this.ruleForm.xianhuatupian = fileUrls;
    },
  }
};
</script>
